*{
  box-sizing: border-box;
}

button{
  font-size: 2.5rem;
  margin: 0 0.1em;
  outline: none;
  border: 0;
  background-color: transparent;
}

ul{
  padding: 0;
}

.navbar{
  background-color: salmon;
  padding: 0.5em;
  font-size: 2rem;  
}

.navbar-logo{
  color: seagreen;
  margin-right: 0.5em;
}
.navbar-count{
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: seagreen;
  color: white;
  border-radius: 50%;
}

.habit{
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-button:hover{
  opacity: 0.8;
}

.habit-count{
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-increase,
.habit-decrease{
  color: goldenrod
}

.habit-delete{
  font-size: 2rem;
  color: darkred;
}

.habits-reset,
.add-button{    
  background-color: seagreen;
  color: white;
  font-size: 1.5em;
  padding: 0.5rem;
  margin: 0 0.5em;
}

.add-input{
  font-size: 1.5em;
}